<template>
  <div class="comSwiepr">
    <wv-swipe :autoplay="3000">
      <!--<wv-swipe-item v-for="(item,index) in srcLists" :key="index"><img :src="item.cover" alt=""></wv-swipe-item>-->
      <wv-swipe-item><img class="bannerImg" src="../assets/img/banner.png" alt=""></wv-swipe-item>
    </wv-swipe>
  </div>
</template>
<script>
  export default {
    name: 'mySwiper',
    data() {
      return {
        srcLists: [],
      }
    },
    props: {
      lists: {
        type: Array,
        default() {
          return []
        }
      },
    },
    watch: {
      lists: function (od, nw) {
        console.log(od);
        this.srcLists = od;
      }
    },
    created() {
    },
  }
</script>
<style>
  .comSwiepr {
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    padding-top: 50px;
    box-sizing: border-box;
  }

  .bannerImg {
    width: 100%;
    height: auto;
  }

  .swiper-container {
    width: 100%;
  }

  .swiper-container img {
    width: 100%;
    height: auto;
    vertical-align: middle;
  }

  .swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 5px !important;
  }

  .swiper-pagination {
    text-align: right !important;
  }

  .weui-grid {
    padding: 15px 15px;
  }
</style>




